<template>
    <div class="content">
        <img :src="image.mt" class="img">
        <div class="boxs">
            <div class="box">
                <div class="hongbao">
                    <div class="hongbao-left">
                        <div class="hongbao-left-son">
                            <div class="hongbao-left-text">
                                <font class="hongbao-left-text1">￥</font>
                                <font class="hongbao-left-text2">{{money}}</font>
                            </div>
                        </div>
                    </div>
                    <div class="hongbao-right">
                        <div class="hongbao-right-son1">
                            <font class="hongbao-right-text1">邀好友得现金</font>
                            <br>
                            <font class="hongbao-right-text2">可提现 不封顶</font>
                        </div>
                        <div class="hongbao-right-son2">
                            赚现金
                        </div>
                    </div>
                </div>
                <div class="desc">
                    登录 APP使用
                </div>

                <div @click="randomMoney" class="btn" data-clipboard-text="Xupc6603eE">
                    <template v-if="money==='?'">领 取 红 包</template>
                    <template v-else>已 领 取</template>
                </div>
            </div>
        </div>



    </div>
</template>

<script>
    import Cookies from 'js-cookie'
    import Clipboard from 'clipboard'
    export default {
        data() {
            return {
                image: {
                    mt: require("../assets/mt.png")
                },
                money: "?"
            }
        },
        mounted() {
            let clipboard = new Clipboard('.btn')
            let nowDate = new Date().toLocaleString().split(" ")[0]
            let cookieDate = Cookies.get("DATE")
            if (nowDate == cookieDate) {
                this.money = 1
            } else {
                this.money = "?"
            }
        },
        methods: {
            randomMoney() {
                if (this.money === '?') {
                    this.money = 1
                    Cookies.set("DATE", new Date().toLocaleString().split(" ")[0], { expires: 1 })
                }
            }
        }
    }
</script>

<style>
    .content {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        max-width: 500px;
        background: #ac3333;

        height: 100vh;
    }

    .img {
        width: 100%;
    }

    .boxs {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        width: 100%;
        padding: 10px;
        box-sizing: border-box
    }

    .box {
        display: flex;
        border-radius: 4px;
        flex-direction: column;
        align-items: center;
        width: 100%;
        background: #ffffff93;
        padding: 10px;
        box-sizing: border-box;
    }

    .hongbao {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        height: 70px;
        box-sizing: border-box;
    }

    .hongbao-left {
        border-radius: 3px 0px 0px 3px;
        background: #fff;
        width: 25%;
        height: 95%;
        padding: 4px 0px 4px 4px;
        box-sizing: border-box;
    }

    .hongbao-left-son {
        border-radius: 3px 0px 0px 3px;
        border: 1px solid #E4E7ED;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
    }

    .hongbao-left-text {
        width: 70%;
        height: 85%;
        color: #ea3248;
        /* background: #606266; */
    }

    .hongbao-left-text1 {
        font-size: 1.1rem;
    }

    .hongbao-left-text2 {
        font-size: 2.3rem;
    }

    .hongbao-right {
        border-radius: 2px 3px 3px 2px;
        background: #eb3022;
        width: 75%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .hongbao-right-son1 {
        width: 50%;
        height: 65%;
        color: #fff;
    }

    .hongbao-right-text1 {
        font-size: 0.9rem;
        font-weight: 600;
    }

    .hongbao-right-text2 {
        color: #eee;
        font-size: 0.7rem;
    }

    .hongbao-right-son2 {
        margin-left: 20px;
        width: 54px;
        height: 26px;
        background: #fdde83;
        border-radius: 13px;
        color: #606266;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 12px;
    }

    .desc {
        margin-top: 10px;
        color: #606266;
        font-size: 0.8rem;
    }

    .btn {
        margin-top: 10px;
        border-radius: 4px;
        width: 100%;
        background: #ff3737;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        font-size: 1.2rem;
        line-height: 2.8rem;
        font-weight: 550;
    }
</style>


